.about {
  padding: 0 20px;
  &__subtitle {
    text-align: center;
    font-family: $fontSecondary;
    font-size: 18px;
    line-height: 1.17;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    color: $paragraph-color;
    margin-bottom: 15px;
  }
  &__title {
    font-family: $fontSecondary;
    font-size: 34px;
    line-height: 1.15;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.62px;
    color: $active-color;
    margin-bottom: 26px;
  }
  &__img {
    display: flex;
    max-width: 100%;
    align-items: center;
    justify-content: center;
    margin-bottom: 46px;
  }
  &__small-block {
    padding: 0 50px;
  }

  @media screen and (min-width: 320px) and (max-width: 480px) {
    &__small-block {
      background-image: url(../images/milk-mobile.png);
      background-repeat: no-repeat;
      background-size: 100%;
      background-position: center;
    }

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      &__small-block {
        background-image: url(../images/milk-mobile@2x.png);
      }
    }
    @media (min-device-pixel-ratio: 3), (min-resolution: 268dpi), (min-resolution: 3dppx) {
      &__small-block {
        background-image: url(../images/milk-mobile@3x.png);
      }
    }
  }
  @media screen and (min-width: 481px) and (max-width: 1199px) {
    &__small-block {
      background-image: disabled;
    }
  }

  @media screen and (min-width: 1200px) and (max-width: 1600px) {
    &__small-block {
      background-image: url(../images/milk-desktop.png);
      background-repeat: no-repeat;
      background-size: 100%;
    }

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      &__small-block {
        background-image: url(../images/milk-desktop@2x.png);
      }
    }
    @media (min-device-pixel-ratio: 3), (min-resolution: 268dpi), (min-resolution: 3dppx) {
      &__small-block {
        background-image: url(../images/milk-desktop@3x.png);
      }
    }
  }
}

.list {
  font-family: $fontPrimary;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;
  color: $text-color-darker;
  &__item {
    padding-bottom: 20px;
    &--marked {
      color: $text-color-bold;
    }
  }
}

.button {
  font-family: $fontPrimary;
  font-weight: 700;
  line-height: 1.3;
  border-radius: 22px;
  border-color: transparent;
  padding: 15px 36px;
  text-align: center;
  color: $active-color;

  &::first-letter {
    text-transform: capitalize;
  }
  &__about {
    margin-bottom: 39px;
  }
}
