.mobile-menu {
    font-family: 'DM Sans';
    font-weight: 700;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding-left: 21px;
    padding-right: 20px;
    background-color: #E17992;
    z-index: 999;
    overflow: scroll;
    transform: translateX(100%);
    transition: transform 250ms ease-in-out;
    &__button-buy {
      width: 209px;
      height: 44px;
      background-color: $text-color-lighter;
      color: $active-color;
      font-size: 16px;
      line-height: 1.31;
      border-radius: 22px;
      text-align: center;
      margin-top: 23px;
    }
  }
  .mobile-menu .is-open {
    transform: translateX(0);
  }
  .mobile-menu .mobile-menu__button-close {
    position: absolute;
    top: 23px;
    right: 28px;
    fill: $text-color-lighter;
  }
  .mobile-nav {
    font-size: 14px;
    line-height: 1.28;
    &__item {
      padding: 12px 0 12px 21px;
      border-top: 1px solid #E18298;
      border-bottom: 1px solid #E18298;
    }
    &__link {
      color: $text-color-lighter;
      &:hover,
      &:focus {
        color: $active-color;
      }
    }
  }